// Using svg

.svg-delete,
.svg-checkbox-tick,
.svg-warning-circle,
.svg-menu,
.svg-tick,
.svg-arrow-previous,
.svg-arrow-next,
.svg-settings,
.svg-search,
.svg-avatar,
.svg-buy {
  position: relative;
}

.svg-delete::after,
.svg-checkbox-tick::after,
.svg-warning-circle::after,
.svg-menu::after,
.svg-tick::after,
.svg-arrow-previous::after,
.svg-arrow-next::after,
.svg-settings::after,
.svg-search::after,
.svg-avatar::after,
.svg-buy::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 1.25rem;
  height: 1.25rem;
  content: "";
  background-repeat: no-repeat;
  background-size: cover;
}

.svg-delete {
  @include svg-delete("#fff");
}

.svg-checkbox-tick {
  @include svg-checkbox-tick("#fff");
}

.svg-warning-circle {
  @include svg-warning-circle(theme-color("info"));
}

.svg-menu {
  @include svg-menu("#fff");
}

.svg-menu:hover {
  @include svg-menu("#000");
}

.svg-tick {
  @include svg-tick(theme-color("success"));
}

.svg-arrow-previous {
  @include svg-arrow-previous("#000");
}

.svg-arrow-previous:hover,
.svg-arrow-previous:focus,
.svg-arrow-previous:active, {
  @include svg-arrow-previous("#fff");
}

.svg-arrow-next {
  @include svg-arrow-next("#000");
}

.svg-arrow-next.btn-primary {
  @include svg-arrow-next("#fff");
}

.svg-arrow-next.btn-primary:active {
  @include svg-arrow-next("#000");
}

.svg-settings {
  @include svg-settings("#fff");
}

.svg-settings:hover,
.svg-settings:focus {
  @include svg-settings(theme-color("primary"));
}

.svg-search {
  @include svg-search("#fff");
}

.svg-search:hover {
  @include svg-search(theme-color("primary"));
}

.svg-avatar {
  @include svg-avatar("#fff");
}

.svg-avatar:hover,
.svg-avatar:active {
  @include svg-avatar(theme-color("primary"));
}

.svg-buy {
  @include svg-buy("#fff");
}

.svg-buy:hover,
.svg-buy:active {
  @include svg-buy(theme-color("primary"));
}
